.write{
    padding-top: 50px;
}


.writeImg{
    margin-top: 20px;
    margin-left: 150px;
    width:70vw;
    height:250px; 
    border-radius: 10px;
    object-fit: cover;
}


.writeForm{
    position: relative;
}

.writeFormGroup{
    margin-top: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.writeIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border:1px solid;
    display: flex;
    align-items: center;
    justify-items: center;
    font-size: 20px;
    color: rgb(129, 125, 125);
    cursor: pointer; 
}


.writeInput{
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}

.writeInput:focus{
    outline:none;
}

.writeText{
    font-size: 20px;
    height: 100vh;
}


.writeSubmit{
    position: absolute;
    top:20px;
    right: 50px;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size:16px;

}

.writeSubmit:hover{
    background-color: rgb(5, 54, 54);
}

